html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}
body.fijar-body {
  overflow: hidden;
}
body.scroll-body {
  padding-top: 13.2rem;
}

p {
  font-size: 2rem;
  color: #000000;
  line-height: 1.5;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h3, h4 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3.2rem;
}

h4 {
  font-size: 2.8rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

.header {
  background-color: #4cb8b3;
  top: 0;
}
@media (min-width: 768px) {
  .header.fija {
    position: fixed;
    width: 100%;
    box-shadow: 0.3rem 0.3rem 0.3rem #333;
    z-index: 1;
    background-color: rgba(76, 184, 179, 0.8);
  }
}
.header h1 {
  color: #ffffff;
  text-align: center;
}
@media (min-width: 768px) {
  .header h1 {
    margin: 0;
  }
}
@media (min-width: 768px) {
  .header .contenido-header {
    display: flex;
    justify-content: space-between;
    padding: 1rem;
    align-items: center;
  }
}

@media (min-width: 768px) {
  .navegacion-principal {
    display: flex;
    gap: 2rem;
  }
}
.navegacion-principal a {
  color: #ffffff;
  font-size: 2.4rem;
  display: block;
  text-align: center;
  margin-top: 1rem;
}
@media (min-width: 768px) {
  .navegacion-principal a {
    margin-top: 0;
  }
}
.navegacion-principal a:hover {
  color: #f53756;
}

.video {
  position: relative;
  max-height: 75rem;
  overflow: hidden;
}
.video .overlay {
  position: absolute;
  background: rgb(75, 0, 186);
  background: linear-gradient(270deg, rgba(75, 0, 186, 0.65) 0%, rgba(99, 13, 128, 0.3) 100%);
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.video video {
  width: auto;
  display: block;
  display: block;
}
@media (min-width: 992px) {
  .video video {
    width: 100%;
  }
}

.contenido-video {
  z-index: 1;
}
.contenido-video h2 {
  color: #ffffff;
  font-size: 6rem;
  margin: 0;
}
.contenido-video p {
  background-color: #f53756;
  display: inline-block;
  padding: 1rem 2rem;
  color: #ffffff;
  font-size: 2.4rem;
  font-weight: bold;
  margin: 0;
}

.sobre-festival {
  padding: 5rem 2rem;
}
@media (min-width: 768px) {
  .sobre-festival {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.contenido-festival {
  margin-top: 5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media (min-width: 768px) {
  .contenido-festival {
    margin-top: 0;
  }
}
.contenido-festival .fecha {
  color: #f53756;
  font-weight: bold;
}

.lineup {
  padding-bottom: 2rem;
  background-color: #f53756;
}
.lineup h3 {
  color: #ffffff;
  text-align: center;
  padding-top: 5rem;
}

.dia {
  color: #ffffff;
  text-align: center;
  padding: 5rem;
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .escenarios-contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.escenario {
  padding-top: 4rem;
  margin-top: 2rem;
  padding-bottom: 2rem;
}
.escenario p {
  text-align: center;
}
.escenario p:after {
  content: "";
  display: block;
  width: 20rem;
  height: 10rem;
  background-size: 15rem;
  background-repeat: no-repeat;
  background-position: center center;
  margin: 2rem auto 0 auto;
}

.icono_edm .nombre-escenario:after {
  background-image: url(../img/icono_edm.png);
}

.icono_ovni .nombre-escenario:after {
  background-image: url(../img/icono_alien.png);
  background-size: 8rem;
}

.bg-amarillo {
  background-color: #fef46d;
}
.bg-amarillo li:nth-child(odd) {
  background-color: #fdec08;
}

.bg-verde {
  background-color: #4cb8b3;
}
.bg-verde li:nth-child(odd) {
  background-color: #3b9691;
}

.nombre {
  text-align: center;
  color: #000000;
  font-weight: bold;
  padding-top: 3rem;
}

.horario {
  margin: 0;
  padding: 0;
  list-style: none;
}
.horario li {
  font-weight: bold;
  padding: 2.5rem;
}
.horario span {
  font-weight: 400;
}

.galeria {
  margin-top: 3rem;
}
.galeria h3 {
  text-align: center;
  text-transform: uppercase;
}

.galeria-imagenes {
  width: auto;
  scale: 1;
  transition: 0.3s;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
.galeria-imagenes picture {
  transition: 0.3s;
}
.galeria-imagenes picture:hover {
  scale: 1.05;
  filter: brightness(80%);
  transition: 0.3s;
  cursor: pointer;
}
@media (min-width: 768px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 992px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}

.overlay {
  z-index: 2;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 5rem;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.5);
}
.overlay picture {
  display: block;
  max-width: 80rem;
}

.btn-cerrar {
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  background-color: #ffffff;
  color: #000000;
  font-weight: bold;
  display: grid;
  place-items: center;
}
.btn-cerrar:hover {
  cursor: pointer;
}

.boletos {
  background-color: #4cb8b3;
}
.boletos h3 {
  color: #ffffff;
  text-align: center;
  padding-top: 5rem;
}
.boletos h4 {
  color: #ffffff;
  text-align: center;
  font-weight: 400;
}

.pases {
  padding: 3rem;
}
.pases h4 {
  margin-top: 3rem;
}
.pases p {
  color: #ffffff;
  text-align: center;
}
@media (min-width: 768px) {
  .pases {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 4rem;
  }
}

.pase {
  margin: 0;
  padding: 0;
  list-style: none;
}
.pase li {
  padding: 1.5rem;
  border-bottom: 1px solid rgba(225, 225, 225, 0.5411764706);
}

.pase-header {
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
  background-color: #f53756;
  font-weight: bold;
}

.pase-contenido {
  background-color: #ffffff;
}

.pase-precio {
  background-color: #752f97;
  color: #ffffff;
  text-align: center;
  font-weight: bold;
}

.site-footer p {
  background-color: #f53756;
  padding: 0.5rem;
  text-align: center;
  margin: 0;
}